.cards {
  display: flex;
  position: absolute;
    top: 29rem;
    left: 23rem;
    right: 10px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.cards::-webkit-scrollbar{
    display: none;
}
.card {
    width: 12rem;
    height: 15rem;
    margin-left: 1rem;
    margin-bottom: 5rem;
    border-radius: 30px;
    background: #ffffff;
    box-shadow:  14px 14px 28px #c6c6c6,
             -14px -14px 28px #ffffff;
}
.img{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
img{
    height: 8rem;
    width: 7rem;
    border-radius: 50%;
    margin-top: 2rem;
}
.text{
    margin-top: 1rem;
    font-weight: 700;


}
